@import "../_globalColor";


body {
     font-size: 18px; 
 }


.dark-mode {
  background-color: $darkBackground;
  color: white;
  transition: "0.1s";
}


.firebase-user {
   background: $userAuth;
   display: block;
   margin: 2px;
   padding: 5px;
   font-size: calc(.5 * (2vh + 1.2vw));
   color: $textColorDark;
   text-align: center;
   overflow-wrap: break-word;
   hyphens: manual;
   line-height: 1.5;

}

.firebase-button {

  background: $buttonAuth;
  color: $buttonAuthColor;
  padding: 1.25% 4.25%;
  transition: box-shadow .3s;
  border: 1px solid #ccc;

}

.firebase-button:hover {

  background:$buttonAuthHoverBG;
  color: $buttonAuthHoverColor;
  box-shadow: 0.245rem 0.245rem black, -0.125rem -0.125rem;
 
}

.user-notice {

 background: $userNoticeBackground;
 color: $userNoticeColor;
 line-height: 1.4;
 text-decoration-style: unset;
 font-weight: 750;
 padding: .5rem;
 margin: 5%;
 -webkit-box-shadow: inset 0px 0px 4px 2px rgba(0, 0, 0, 0.6);
 -moz-box-shadow: inset 0px 0px 4px 2px rgba(0, 0, 0, 0.6);
 box-shadow: inset 0px 0px 4px 2px rgba(0, 0, 0, 0.6);
}

.successful-auth-welcome {
  font-weight: 750;
}

.firebase-email {
  margin-left: 1rem;

}

.firebase-user-auth {
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 6% 25%;
  line-height: 1.5;
  color: $spaceWhite;
  text-align: center;
  overflow-wrap: break-word;
  hyphens: manual;

}

.space-background-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.firebase-user-auth-h1 {
  font-size: 1.4rem;
  margin-bottom: 2rem;
  text-align: center;
  color: $spaceWhite;
  font-weight: 800;

}

.firebaseui-list-item {
  padding: 4%;
}

.firebaseui-list-item:hover {

  opacity: 0.25;
}

h3 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

.user-notice {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  text-align: center;
}


@media (max-width: 380px)  {

  .firebase-user-auth {
   font-size: .8rem;
  }
  .firebase-user-auth-h1 {
    font-size: 1.2rem;
  }
}


@media (max-width: 250px)  {

  .firebase-user-auth {
   font-size: .7rem;
  }
  .firebase-user-auth-h1 {
    font-size: 1rem;
  }
}



@import url("https://fonts.googleapis.com/css?family=Raleway");

:root {
  --glow-color: hsl(186 100% 69%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.glowing-btn {
  position: relative;
  color: var(--glow-color);
  cursor: pointer;
  padding: 0.35em 1em;
  border: 0.15em solid var(--glow-color);
  border-radius: 0.45em;
  background: none;
  perspective: 2em;
  font-family: "Raleway", sans-serif;
  font-size: 1.5em;
  font-weight: 900;
  letter-spacing: 1em;

  -webkit-box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  -moz-box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  animation: border-flicker 2s linear infinite;
}

.glowing-txt {
  float: left;
  margin-right: -0.8em;
  -webkit-text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3),
    0 0 0.45em var(--glow-color);
  -moz-text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3),
    0 0 0.45em var(--glow-color);
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em var(--glow-color);
  animation: text-flicker 3s linear infinite;
}

.faulty-letter {
  opacity: 0.5;
  animation: faulty-flicker 2s linear infinite;
}

.glowing-btn::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.7;
  filter: blur(1em);
  transform: translateY(120%) rotateX(95deg) scale(1, 0.35);
  background: var(--glow-color);
  pointer-events: none;
}

.glowing-btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: -1;
  background-color: var(--glow-color);
  box-shadow: 0 0 2em 0.2em var(--glow-color);
  transition: opacity 100ms linear;
}

.glowing-btn:hover {
  color: rgba(0, 0, 0, 0.8);
  text-shadow: none;
  animation: none;
}

.glowing-btn:hover .glowing-txt {
  animation: none;
}

.glowing-btn:hover .faulty-letter {
  animation: none;
  text-shadow: none;
  opacity: 1;
}

.glowing-btn:hover:before {
  filter: blur(1.5em);
  opacity: 1;
}

.glowing-btn:hover:after {
  opacity: 1;
}

@keyframes faulty-flicker {
  0% {
    opacity: 0.1;
  }
  2% {
    opacity: 0.1;
  }
  4% {
    opacity: 0.5;
  }
  19% {
    opacity: 0.5;
  }
  21% {
    opacity: 0.1;
  }
  23% {
    opacity: 1;
  }
  80% {
    opacity: 0.5;
  }
  83% {
    opacity: 0.4;
  }

  87% {
    opacity: 1;
  }
}

@keyframes text-flicker {
  0% {
    opacity: 0.1;
  }

  2% {
    opacity: 1;
  }

  8% {
    opacity: 0.1;
  }

  9% {
    opacity: 1;
  }

  12% {
    opacity: 0.1;
  }
  20% {
    opacity: 1;
  }
  25% {
    opacity: 0.3;
  }
  30% {
    opacity: 1;
  }

  70% {
    opacity: 0.7;
  }
  72% {
    opacity: 0.2;
  }

  77% {
    opacity: 0.9;
  }
  100% {
    opacity: 0.9;
  }
}

@keyframes border-flicker {
  0% {
    opacity: 0.1;
  }
  2% {
    opacity: 1;
  }
  4% {
    opacity: 0.1;
  }

  8% {
    opacity: 1;
  }
  70% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 600px) {
  .glowing-btn{
    font-size: 1em;
  }
}
